@tailwind base;
@tailwind components;
@tailwind utilities;

*{
  box-sizing: border-box;
}
html{
  font-size: 1.25em;
}
body {
  font-family: 'Rubik', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: auto;
  line-height: 1.75;
}

input {
  border-radius: 0;
  -webkit-appearance: none;
  -webkit-border-radius: 0;
}

::-webkit-scrollbar {
  display: none;
}